import React, { useState } from "react";
import "./styles.css";


const CoinInfo = ({ name, desc })=> {
  const [flag, setFlag] = useState(false);

  const smallDesc =
    desc.length > 400
      ? desc.slice(0, 400) +
        "<p style='color:var(--grey); cursor:pointer;'>Read More...</p>"
      : desc;
  const fullDesc =
    desc.length > 400
      ? desc + "<p style='color:var(--grey);cursor:pointer;'>Read Less...</p>"
      : desc;

  return (
    <div className="CoinInfo">
      <h1 className="CoinInfo-heading">{name}</h1>
      <p
        onClick={() => {
          desc.length > 400 && setFlag(!flag);
        }}
        className="CoinInfo-desc"
        dangerouslySetInnerHTML={{ __html: flag ? fullDesc : smallDesc }}
      />
    </div>
  );
}

export default CoinInfo;
